import React, { useState, useRef, useCallback, forwardRef } from 'react'
import { Waypoint } from 'react-waypoint';
import range from 'lodash/range'

import Container from '../components/Container'
import Button from '../components/Button'
import Box from '../components/Box'
import Flex from '../components/Flex'
import Sticky from '../components/Sticky'

import { responsive } from '../components/ThemeProvider/theme'

import Quality from './Quality'
import Diversity from './Diversity'
import Risk from './Risk'

const secs = [
  { name: '堅持品質', Comp: Quality },
  { name: '多元佈局', Comp: Diversity },
  { name: '平衡風險', Comp: Risk },
].map(({ name, Comp }) => ({
  name,
  Comp: forwardRef((p, ref) => <Comp {...p} innerRef={ref} />)
}))

const secLen = secs.length

const Sections = ({ isMobile }) => {
  const [ nowId, setId ] = useState(0)
  const refs = range(secLen).map(useRef)
  const handleOnClick = useCallback((index) => {
    if (refs[index] && refs[index].current) {
      refs[index].current.scrollIntoView({ behavior: 'smooth' });
    }
  }, [refs])
  const sections = (
    <Flex flexDirection={responsive('column', 'row')}>
      {secs.map(({ name }, index) => (
        <Button.darkGreen
          key={name}
          width={1}
          borderRight={isMobile ? 'none' : (index === secLen - 1 ? 'none' : '0.5px solid')}
          borderLeft={isMobile ? 'none' : (index ? '0.5px solid' : 'none')}
          borderBottom={isMobile ? (index === secLen - 1 ? '1px solid' : '0.5px solid') : 'none' }
          borderTop={isMobile ? (index ? '0.5px solid' : '1px solid') : 'none' }
          onClick={() => handleOnClick(index)}
          active={nowId === index}
        >
          {name}
        </Button.darkGreen>
      ))}
    </Flex>
  )
  return (
    <div>
      <Box is={isMobile ? 'div' : Sticky}>
        <Box bg="darkGreen" textAlign="center">
          {isMobile ? sections : <Container>{sections}</Container>}
        </Box>
      </Box>
      {secs.map(({ Comp, name }, index) => (
        <div key={`sec-${name}`} ref={refs[index]}>
          <Waypoint onEnter={() => setId(index)}>
            <Comp />
          </Waypoint>
        </div>
      ))}
    </div>
  )
}

export default Sections
