import React from 'react'

import Container from './Container'
import Box from './Box'
import Flex from './Flex'
import Image from './Image'
import Text from './Text'

const Title = ({ src, step, title, ...props }) => {
  return (
    <Box bg="lightGray" py={['2.85em', null, '4.43em']} {...props}>
      <Container>
        <Flex alignItems="center">
          <Box width={['5.715em', null, '10%']} >
            <Image src={src} />
          </Box>
          <Box fontWeight="bold" ml={['0.86em', null, '1.86em']} >
            {step && <Text fontSize={['1.428em', null, '1.86em']} color="bgGreen">策略{step}</Text>}
            <Text fontSize={['1.85em', null, '2.86em']}>{title}</Text>
          </Box>
        </Flex>
      </Container>
    </Box>
  )
}

export default Title
